import React, { useState } from "react";
import { Button } from "@mui/material";

import PopUpCall from "../../components/PopUpCall";

const MidBannerTwo = ({ className }) => {
  const [callModal, setCallModal] = useState(false),
    openCallModal = () => setCallModal(true),
    closeCallModal = (event, reason) => {
      setCallModal(false);
    };

  return (
    <section className={className}>
      <div className="content">
        <h4>
          {"Have questions about "}
          <br />
          {"plans & pricing?"}
        </h4>
        <p>
          {
            "Our sales team will be delighted to answer all your queries about the product, features, pricing and customizations."
          }
        </p>
        <Button variant="contained" onClick={openCallModal} className="action">
          {"Talk to Sales"}
        </Button>
      </div>
      <PopUpCall open={callModal} onClose={closeCallModal} />
    </section>
  );
};

export default MidBannerTwo;
